.floating-menu {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 100%;
  height: 2.1rem !important;
  width: 2.1rem !important;
  pointer-events: auto !important;
  display: flex;
  position: fixed;
  background-color: white !important;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.floating-menu-item {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 100%;
  height: 2.1rem;
  width: 2.1rem;
  pointer-events: auto !important;
  display: flex;
  opacity: 1;
  position: static !important;
  position: initial !important;
  background-color: white !important;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease-in;
}

.floating-menu > svg {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 0;
  height: 32px;
  margin: auto;
  width: 32px;
